export default {
	data() {
		return {
			baseUrl:this.$common.baseUrl,
			modalName: null,
			tempTagArr:[],
			langch:{
				zizhanghao:'子账号',
				tianjia:'添加子账号',
				qiehuan:'切换主账号',
				queding:'确定',
				quxiao:'取消',
			},
			langen:{
				zizhanghao:'Sub account',
				tianjia:'add account',
				qiehuan:'toggle mainaccount',
				queding:'Determine',
				quxiao:'Cancel',
			},
			avatar:uni.getStorageSync('userinfo').avatar,
			username:uni.getStorageSync('userinfo').username,
			langtype:'',
			// 当前子账户名称
			currentAccount:'',
			// 添加子账户账户名
			addsubAccount:'',
			// 获取子账户数组
			subAccountArrray:[],
			// 是否切换主账号按钮状态
			changemainbtn:true
		}
	},
	onShow() {
		this.langtype = uni.getStorageSync('lang')?uni.getStorageSync('lang'):'CH',
		this._getsubAccount()
	},
	methods: {
		// 获取子账户
		_getsubAccount(){
			this.$common.requests('/api/child/child_list').then(res=>{
				this.subAccountArrray = res.data.data
			})
		},
		addNewList(){
			
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			// 当点击确定提交创建子账户
			if(e == '确定'){
				let obj ={
					nickname:this.addsubAccount
				}
				var that = this
				this.$common.requests('/api/child/add_child',obj).then(res=>{
					if(res.data.code == 1){
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
						// 删除添加否重新获取子账户
						that._getsubAccount()
					}
					if(res.data.code ==0){
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				})
			}
			this.modalName = null
		},
		// 删除子账户
		deleteclick(id){
			let obj ={
				child_id:id
			}
			var that = this
			this.$common.requests('/api/child/del_child',obj).then(res=>{
				if(res.data.code == 1){
					uni.showToast({
						title:res.data.msg,
						icon:'none'
					})
					// 删除添加否重新获取子账户
					that._getsubAccount()
				}
			})
		},
		// 切换子账号
		changeAccount(id,nickname){
			this.currentAccount = nickname
			let obj ={
				child_id:id
			}
			var that = this
			this.$common.requests('/api/child/check_child',obj).then(res=>{
				uni.removeStorageSync('uid')
				uni.removeStorageSync('token')
				uni.removeStorageSync('userinfo')
				if(res.data.code==1){
					// console.log(res.data.data)
					var data = res.data.data.userinfo;
					uni.setStorageSync('uid', data.user_id);
					uni.setStorageSync('token', data.token);
					uni.setStorageSync('userinfo', data);
					// uni.navigateTo({
					// 	url: '../personalCenter/personalCenter'
					// })
					// 把切换主账号高亮
					that.changemainbtn = false
				}
			})
		},
		// 切换主账号
		toggle(){		
			this.currentAccount = ''
			var that = this
			var mysubtoken = uni.getStorageSync('token');
			let obj = {
				token:mysubtoken
			}
			this.$common.requests('/api/child/check_father',obj).then(res=>{
				uni.removeStorageSync('uid')
				uni.removeStorageSync('token')
				uni.removeStorageSync('userinfo')
				if(res.data.code==1){
					var data = res.data.data.userinfo;
					uni.setStorageSync('uid', data.user_id);
					uni.setStorageSync('token', data.token);
					uni.setStorageSync('userinfo', data);
					
					that.changemainbtn = true
					console.log(that.changemainbtn)
				}
			})
		},
		back(){
			 uni.navigateBack({
				delta:1
			 })
		},
	}
}
